import {Sort} from "./Sort";
import {BinaryInsertionSort} from "./BinaryInsertionSort";

export class SimpleSort extends Sort{
    constructor(arrayVisualizer) {
        super(arrayVisualizer);
        this.sortName = "SimpleSort"
        this.warnLen = 200
    }

    minRunLength(n) {
        let r = 0

        while (n >= 16) {
            r |= (n & 1)
            n >>= 1
        }

        return n + r
    }

    SimpleSort(low, high) {

        let len = high-low

        let pos;
        let current;

        let minRun = this.minRunLength(len)

        let binaryInsertion = new BinaryInsertionSort(this.arrayVisualizer)

        for (let i = 0; i < len/minRun;++i){
            binaryInsertion.binaryInsertionSort(i*minRun, Math.min((i+1)*minRun, len)+1, i*minRun)
        }

        // binaryInsertion.binaryInsertionSort(low, Math.trunc(len)*0.1+low, 0)
        //
        // binaryInsertion.binaryInsertionSort(high - Math.trunc(len)*0.1, high+1, high - Math.trunc(len))

    }

    runSort(low, high) {
        this.SimpleSort(low, high)
    }
}